
* {
    box-sizing: border-box;
}

.Loading {
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 300;

}

.Loading .loading_text {
    color: black;
    font-size: 30px;
    font-style: italic;
}

.movies {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 80%;
}

.container {
    height: 100%;
    display: flex;
}